"use client";

import { Card } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { useState } from "react";

interface Event {
  id: number;
  title: string;
  date: string;
  time: string;
  description: string;
}

const initialEvents: Event[] = [
  {
    id: 1,
    title: "Reunião de Pauta",
    date: "2024-01-20",
    time: "14:00",
    description: "Discussão sobre os próximos temas do podcast",
  },
  {
    id: 2,
    title: "Gravação Especial",
    date: "2024-01-22",
    time: "15:30",
    description: "Episódio especial com convidado internacional",
  },
];

export default function Home() {
  const [events, setEvents] = useState<Event[]>(initialEvents);
  const [showForm, setShowForm] = useState(false);
  const [formData, setFormData] = useState({
    title: "",
    date: "",
    time: "",
    description: "",
  });

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    const newEvent = {
      id: events.length + 1,
      ...formData,
    };
    setEvents([...events, newEvent]);
    setFormData({ title: "", date: "", time: "", description: "" });
    setShowForm(false);
  };

  return (
    <main className="min-h-screen bg-gradient-to-br from-[#000428] to-[#004e92] text-cyan-300">
      {/* Header Section */}
      <nav className="p-6 backdrop-blur-sm bg-black/10">
        <div className="max-w-7xl mx-auto flex justify-between items-center">
          <h1 className="text-3xl font-bold tracking-wider text-cyan-300 drop-shadow-[0_0_10px_rgba(0,255,255,0.3)]">
            Agenda Presidencial
          </h1>
          <Button 
            onClick={() => setShowForm(!showForm)}
            className="rounded-full bg-cyan-500/20 hover:bg-cyan-500/30 text-cyan-300 border border-cyan-500/50 hover:shadow-[0_0_15px_rgba(0,255,255,0.3)] transition-all duration-300"
          >
            {showForm ? "Fechar" : "Novo Evento"}
          </Button>
        </div>
      </nav>

      {/* Form Section */}
      {showForm && (
        <div className="max-w-2xl mx-auto mt-8 p-6">
          <form onSubmit={handleSubmit} className="space-y-4 backdrop-blur-md bg-white/5 rounded-2xl p-6 shadow-[0_0_15px_rgba(0,0,0,0.2)]">
            <div>
              <label className="block text-cyan-200 mb-2">Título</label>
              <input
                type="text"
                value={formData.title}
                onChange={(e) => setFormData({ ...formData, title: e.target.value })}
                className="w-full p-2 rounded-lg bg-black/20 border border-cyan-500/30 text-cyan-100 focus:outline-none focus:ring-2 focus:ring-cyan-500/50"
                required
              />
            </div>
            <div>
              <label className="block text-cyan-200 mb-2">Data</label>
              <input
                type="date"
                value={formData.date}
                onChange={(e) => setFormData({ ...formData, date: e.target.value })}
                className="w-full p-2 rounded-lg bg-black/20 border border-cyan-500/30 text-cyan-100 focus:outline-none focus:ring-2 focus:ring-cyan-500/50"
                required
              />
            </div>
            <div>
              <label className="block text-cyan-200 mb-2">Horário</label>
              <input
                type="time"
                value={formData.time}
                onChange={(e) => setFormData({ ...formData, time: e.target.value })}
                className="w-full p-2 rounded-lg bg-black/20 border border-cyan-500/30 text-cyan-100 focus:outline-none focus:ring-2 focus:ring-cyan-500/50"
                required
              />
            </div>
            <div>
              <label className="block text-cyan-200 mb-2">Descrição</label>
              <textarea
                value={formData.description}
                onChange={(e) => setFormData({ ...formData, description: e.target.value })}
                className="w-full p-2 rounded-lg bg-black/20 border border-cyan-500/30 text-cyan-100 focus:outline-none focus:ring-2 focus:ring-cyan-500/50 h-24"
                required
              />
            </div>
            <Button 
              type="submit"
              className="w-full rounded-full bg-cyan-500/20 hover:bg-cyan-500/30 text-cyan-300 border border-cyan-500/50 hover:shadow-[0_0_15px_rgba(0,255,255,0.3)] transition-all duration-300"
            >
              Adicionar Evento
            </Button>
          </form>
        </div>
      )}

      {/* Events Grid */}
      <div className="max-w-7xl mx-auto p-6 grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6 mt-8">
        {events.map((event) => (
          <Card 
            key={event.id}
            className="backdrop-blur-md bg-white/5 border-cyan-500/20 hover:shadow-[0_0_20px_rgba(0,255,255,0.15)] transition-all duration-300"
          >
            <div className="p-6">
              <h3 className="text-xl font-semibold text-cyan-200 mb-2">{event.title}</h3>
              <div className="flex items-center gap-4 text-cyan-400 mb-4">
                <span>{new Date(event.date).toLocaleDateString("pt-BR")}</span>
                <span>{event.time}</span>
              </div>
              <p className="text-cyan-300/80">{event.description}</p>
            </div>
          </Card>
        ))}
      </div>
    </main>
  );
}
